import CSGNodeCalculator from "../csg/csgNodeCalculator";
import {CSGCommandKind} from "./CSGCommandKind";
import {CSGNodeMetadataKind} from "../csg/csgTypes";

export class CommandExecutor {
    
    geometry : any
    nodeCalculator: CSGNodeCalculator
    
    
    constructor(geometry : any, nodeCalculator : CSGNodeCalculator) {
        this.geometry = geometry;
        this.nodeCalculator = nodeCalculator;
    }
    
    executeCommandJson(commandJson:string) : string
    {
        if (typeof commandJson !== 'string')
        {
            throw new Error("executeCommandJson expects string input.");
        }
            
        const command = JSON.parse(commandJson);
       return this.executeCommand(command);
    }
    
    executeCommand(command : any)
    {
        let result;
        
        switch(command.Command)
        {
            case CSGCommandKind.GetTopoShapeMetadata:
                result = this.getTopoShapeMetadata(command);
                break;
        }

        let resultJson = JSON.stringify(result, null, 2);
        return resultJson;
    }
    
    getTopoShapeMetadata(command : any) : any
    {
        let result;
        
        switch(command.Kind)
        {
            case CSGNodeMetadataKind.Mesh:
                result = this.getMesh(command);
                break;
                
            case CSGNodeMetadataKind.AABB:
                result = this.getAABB(command);
                break;
                
            case CSGNodeMetadataKind.OBB:
                result = this.getOBB(command);
                break;
        }
        
        return result;
    }
    
    getAABB(command : any) : any
    {
        const shape = this.nodeCalculator.calculateNodeShapesWithCache(command.Node)[0];
        let aabb = this.geometry.boundingBoxes.getAABB(shape, false);
        return aabb;
    }

    getOBB(command : any) : any
    {
        const shape = this.nodeCalculator.calculateNodeShapesWithCache(command.Node)[0];
        let obb = this.geometry.boundingBoxes.getOBB(shape, false);
        return obb;
    }

    getMesh(command : any) : any 
    {
        const shape = this.nodeCalculator.calculateNodeShapesWithCache(command.Node)[0];

        let facesAndEdges = this.geometry.makeIntoSingleMesh_My(shape, true, command.TheLinDeflection, command.TheAngDeflection);

        let faceListData = {faces: facesAndEdges.faces};
        let edgeListData = {edges: facesAndEdges.edges};

        let mesh = {
            faceListData,
            edgeListData
        }; 
        
        return mesh;
    }
}