import {
    BRepOffsetAPI_MakeThickSolid, GeomAbs_JoinType, Message_ProgressRange, Message_ProgressRange_1,
    OpenCascadeInstance, TopoDS_Face,
    TopoDS_Shape,
    TopTools_ListOfShape,
    TopTools_ListOfShape_1
} from "opencascade.js/dist/opencascade.full";
import och from "../common/openCascadeHelper";
import utils from "../common/utils";
const solidOperations = {
    oc: null as OpenCascadeInstance | null,

    setOpenCascade(openCascade: OpenCascadeInstance) {
        this.oc = openCascade;
    },

    makeHollow(shape: TopoDS_Shape, faceIndexes: number[], wallThickness : number, joinType : string) {
        const oc = this.oc!;
        
        let allFaces : TopoDS_Face[] = och.ListFaces(shape);
        let faces = faceIndexes.map(index => allFaces[index]);
        
        let facesToRemove : TopTools_ListOfShape = new oc.TopTools_ListOfShape_1();
        faces.forEach(face => { facesToRemove.Append_1(face); });

        let progressRange : Message_ProgressRange = new oc.Message_ProgressRange_1();
        
        let aSolidMaker : BRepOffsetAPI_MakeThickSolid = new oc.BRepOffsetAPI_MakeThickSolid()
        
        let intersection : boolean = true;
        let selfIntersection : boolean = true;
        let geomJointType : GeomAbs_JoinType = utils.parseJoinType(joinType) as any;
        let removeEdges : boolean = false;
            
        aSolidMaker.MakeThickSolidByJoin(shape,
            facesToRemove,
            wallThickness,
            oc.Precision.Confusion(),
            
            oc.BRepOffset_Mode.BRepOffset_Skin as any,
            // oc.BRepOffset_Mode.BRepOffset_Pipe as any,
            // oc.BRepOffset_Mode.BRepOffset_RectoVerso as any,
            
            intersection,
            selfIntersection,
            
            geomJointType,
            removeEdges,
            progressRange
            );
       
        allFaces.forEach( face => { face.delete() });
        facesToRemove.delete();
            
        let hollowed = aSolidMaker.Shape();
        return hollowed;
    },
};

export default solidOperations;
