import geometry from './../common/geometry.js'

export function testUnion()
{
    let box = geometry.makeBox(0,0,0,10,20,30);
    // console.log("box: ",box);
    let sphere = geometry.makeSphere(0,0,0,15);
    // console.log("sphere: ",sphere);
    let union = geometry.booleans.makeUnion([box, sphere],true);
    // console.log("union: ",union);
    
    return union;
}

export function testDifference()
{
    let box = geometry.makeBox(0,0,0,10,20,30);
    //// console.log("box: ",box);
    
    let sphere = geometry.makeSphere(0,0,0,15);
    //// console.log("sphere: ",sphere);
    
    let difference = geometry.makeDifference(box,[sphere],true);
    //// console.log("difference: ",difference);

    return difference;
}

export function testCompounds()
{
    let box0 = geometry.makeBox(0,0,0,10,20,30);
    let box1 = geometry.makeBox(0,0,0,40,50,60);
    let box2 = geometry.makeBox(0,0,0,70,80,90);
    let box3 = geometry.makeBox(0,0,0,100,110,120);
    
    let compound0 = geometry.compounds.makeCompound([box0, box1]);
    let compound1 = geometry.compounds.makeCompound([box2, box3]);
    
    let compoundOfCompounds = geometry.compounds.makeCompound([compound0, compound1]);
    // console.log("compoundOfCompounds is: ",compoundOfCompounds)
    // console.log("compoundOfCompounds type is: ",geometry.shapes.getShapeTypeAsString(compoundOfCompounds));

    let listed = geometry.compounds.listCompoundShapes(compoundOfCompounds, geometry.openCascade.TopAbs_ShapeEnum.TopAbs_COMPOUND);
    // console.log("listed type is: ",listed);
}