import { CSGNodeType, CSGOperationType, CSGFaceType } from './csgTypes'

export default class CSGFaceFactory
{
    #geometry;
    #nodeCalculator;
    
    constructor(geometry, nodeCalculator) {
        this.#geometry = geometry;
        this.#nodeCalculator = nodeCalculator;
    }

    /**
        return: a shape.
     */
    createNodeShape(node) {
        if (node.Type !== CSGNodeType.Face) throw new Error(`Given node is not a ${CSGNodeType.Face}`);

        let shape = undefined;

        switch(node.Face.Type)
        {
            case CSGFaceType.FaceFromWires:
                shape = this.#createFaceFromWires(node.Face.WireNodes);
                break;

            case CSGFaceType.SelectFaceFromShapeByAxis:
                break;

            case CSGFaceType.SelectFaceByIndex:
                break;
                
            default:
                throw new Error(`Face type ${node.Face.Type} is not supported.`)
        }

        return shape;
    }

    #createFaceFromWires(wireNodes) {

        let wireShapes = wireNodes.map(wireNode => {
            let wireShape = this.#nodeCalculator.calculateNodeShapesWithCache(wireNode)[0];
            return wireShape;
        });
        
        let face = this.#geometry.faces.wiresToFaces(wireShapes);
        return face;
    }
}