import { CSGNodeType, CSGOperationType, CSGSolidType, CSGWireType } from './csgTypes'

export default class CSGPlaneFactory
{
    #geometry;
    
    constructor(geometry) {
        this.#geometry = geometry;
    }

    /**
        return: a shape.
     */
    createNodeShape(node) {
        if (node.Type !== CSGNodeType.Plane) throw new Error(`Given node is not a ${CSGNodeType.Plane}`);

        let shape = this.#createPlaneShape(node.Plane);
        return shape;
    }

    #createPlaneShape(plane) {
        let shape = this.#geometry.makePlane(plane.Normal, plane.Point, plane.Width, plane.Height, 0.5, 0.5);
        
        shape.userData = {
            planePoint: plane.Point,
            planeNormal: plane.Normal,
            planeWidth: plane.Width,
            planeHeight: plane.Height
        }
        
        return shape;
    }
}