import {CSGNodeType, CSGOperationType, CSGFaceType, CSGVertexType} from './csgTypes'

export default class CSGVertexFactory
{
    #geometry;
    #nodeCalculator;
    
    constructor(geometry, nodeCalculator) {
        this.#geometry = geometry;
        this.#nodeCalculator = nodeCalculator;
    }

    /**
        return: a shape.
     */
    createNodeShape(node) {
        if (node.Type !== CSGNodeType.Vertex) throw new Error(`Given node is not a ${CSGNodeType.Vertex}`);

        let shape = undefined;

        switch(node.Vertex.Type)
        {
            case CSGVertexType.FromPoint:
                shape = this.#createVertexFromPoint(node.Vertex);
                break;

            case CSGVertexType.FromToShapeProjection:
                shape = this.#createVertexFromToShapeProjection(node.Vertex);
                break;

            case CSGVertexType.FromClosestPointOnShapeProjection:
                shape = this.#createVertexFromClosestPointOnShapeProjection(node.Vertex);
                break;
                
            default:
                throw new Error(`Vertex type ${node.Vertex.Type} is not supported.`);
        }
        return shape;
    }
    
    #createVertexFromPoint(vertex) {
        let vertexShape = this.#geometry.projections.createVertexFromPoint(vertex.Point);
        return vertexShape;
    }
    
    #createVertexFromToShapeProjection(vertex) {
        let projectionTargetShape = this.#nodeCalculator.calculateNodeShapesWithCache(vertex.Shape)[0];
        let { intersectionFound, point, normal } = this.#geometry.projections.projectPointOntoShape(vertex.Point, vertex.Direction, projectionTargetShape);
        
        console.log("if: "+intersectionFound +" p: "+point);
        
        if (!intersectionFound)
        {
            throw new Error("No intersection found.");
        }
        
        let vertexShape = this.#geometry.projections.createVertexFrom_gp_Pnt(point);
        
        vertexShape.userData = {
          normal: [normal.X(), normal.Y(), normal.Z()]
        };
        
        return vertexShape;
    }

    #createVertexFromClosestPointOnShapeProjection(vertex) {
        let projectionTargetShape = this.#nodeCalculator.calculateNodeShapesWithCache(vertex.Shape)[0];
        let projectedPoint = this.#geometry.projections.findClosestPointOnShape(vertex.Point, projectionTargetShape);
        let vertexShape = this.#geometry.projections.createVertexFrom_gp_Pnt(projectedPoint);
        return vertexShape;
    }
}