import {CSGNodeType, CSGWireOperationType} from "./csgTypes";
import ff from './../common/featureFlags'

export default class CSGWireOperationCalculator
{
    #geometry;
    #nodeCalculator;
    
    constructor(geometry, nodeCalculator) {
        this.#geometry = geometry;
        this.#nodeCalculator = nodeCalculator;
    }
    
    calculateOperation(node) {
        if (node.Type !== CSGNodeType.WireOperation) throw new Error(`Given node is not an ${CSGNodeType.WireOperation}`);
        
        let shapes = undefined;

        switch(node.OperationType)
        {
            case CSGWireOperationType.Project:
                shapes = this.#project(node);
                break;
                
            case CSGWireOperationType.Offset:
                shapes = this.#offsetWire(node);
                break;

            default:
                throw new Error(`Wire operation ${node.Operation} is unsupported yet.`);
        }

        if (ff.attachCSGNodeToShapeResult)
        {
            shapes.forEach(shape => {
                shape._node = node;
            });
        }

        return shapes;
    }

    #project(node) {
        
        let wireNode = node.Wire;
        let targetNode = node.ProjectionTarget;
        let direction = node.Direction;
        
        let wireShape = this.#nodeCalculator.calculateNodeShapesWithCache(wireNode)[0];
        let targetShape = this.#nodeCalculator.calculateNodeShapesWithCache(targetNode)[0];
        
        let result = this.#geometry.wires.projectWireOntoFace(wireShape, targetShape, direction); 
        return [ result ];
    }

    #offsetWire(node) {
        let wireNode = node.Wire;
        let offset = node.Offset;
        let joinType = node.JoinType;

        let wireShape = this.#nodeCalculator.calculateNodeShapesWithCache(wireNode)[0];

        let result = this.#geometry.wires.offsetWire(wireShape, offset, joinType);
        return [ result ];
    }
}
