import math from "../common/math";
import compounds from "../common/compounds";
import {
    OpenCascadeInstance,
    TopoDS_Shape,
    Bnd_Box, Bnd_OBB, gp_Pnt, gp_Dir, gp_XYZ
} from "opencascade.js/dist/opencascade.full";

const boundingBoxes = {
    oc: null as OpenCascadeInstance | null,

    setOpenCascade(openCascade: OpenCascadeInstance) {
        this.oc = openCascade;
    },

    getOBB(shape: TopoDS_Shape, useTriangulation : boolean)
    {
        const oc = this.oc!;

        useTriangulation = useTriangulation ?? true;

        let obb : Bnd_OBB = new oc.Bnd_OBB_1();
        oc.BRepBndLib.AddOBB(shape, obb, useTriangulation, true, true);

        let obbox = {
            isValid: false,
            center: [NaN, NaN, NaN],
            hsize: [NaN, NaN, NaN],
            xdir: [NaN, NaN, NaN],
            ydir: [NaN, NaN, NaN],
            zdir: [NaN, NaN, NaN]
        };
        
        if (!obb.IsVoid()) {

            let center: gp_XYZ = obb.Center();

            let xhSize: number = obb.XHSize();
            let yhSize: number = obb.YHSize();
            let zhSize: number = obb.ZHSize();

            let xDir: gp_XYZ = obb.XDirection(); // X direction
            let yDir: gp_XYZ = obb.YDirection(); // Y direction
            let zDir: gp_XYZ = obb.ZDirection(); // Z direction

            obbox.isValid = true;
            obbox.center = [center.X(), center.Y(), center.Z()];
            obbox.hsize = [xhSize, yhSize, zhSize];
            obbox.xdir = [xDir.X(), xDir.Y(), xDir.Z()];
            obbox.ydir = [yDir.X(), yDir.Y(), yDir.Z()];
            obbox.zdir = [zDir.X(), zDir.Y(), zDir.Z()];
        }


        obb.delete();
        return obbox;
    },
    
    getAABB(shape: TopoDS_Shape, useTriangulation : boolean) {
        const oc = this.oc!;

        useTriangulation = useTriangulation ?? true;
        
        let aabb : Bnd_Box = new oc.Bnd_Box_1();
        oc.BRepBndLib.Add(shape, aabb, useTriangulation);

        let aabbox = { isValid: false, min: [NaN, NaN, NaN], max: [NaN, NaN, NaN]};
        if (!aabb.IsVoid()) {

            let xmin = {current: 0}, ymin = {current: 0}, zmin = {current: 0},
                xmax = {current: 0}, ymax = {current: 0}, zmax = {current: 0};
            
            aabb.Get(xmin as any, ymin as any, zmin as any, xmax as any, ymax as any, zmax as any);

            aabbox.isValid = true;
            aabbox.min = [xmin.current, ymin.current, zmin.current] as any;
            aabbox.max = [xmax.current, ymax.current, zmax.current] as any;
        }
        
        aabb.delete();
        
        return aabbox;
    },
};

export default boundingBoxes;
