import {CSGNodeType, CSGOperationType} from "./csgTypes";
import math from './../common/math.js'

export default class CSGTransformCalculator
{
    #geometry;
    #nodeCalculator;
    
    constructor(geometry, nodeCalculator) {
        this.#geometry = geometry;
        this.#nodeCalculator = nodeCalculator;
    }
    
    calculateTransforms(node) {
        if (node.Type !== CSGNodeType.Transform) throw new Error(`Given node is not an ${CSGNodeType.Transform}`);

        let targetShapes = this.#nodeCalculator.calculateNodeShapesWithCache(node.Node);
        let transformedShapes = targetShapes.map(targetShape => this.#transformShape(targetShape, node));
        return transformedShapes;
    }
    
    #transformShape(shape, node) {
        let transformedShape = shape;
        
        if (node.Append === false)
        {
            transformedShape = this.#geometry.resetLocation(transformedShape);
        }

        const isScaleIdentity = node.Transform.scale[0] === 1 && node.Transform.scale[1] === 1 && node.Transform.scale[2] === 1;
        if (node.UseS && !isScaleIdentity)
        {
            transformedShape = this.#geometry.makeScale(transformedShape, node.Transform.scale);
        }

        const isRotationZero = node.Transform.rotation[3] <= math.kEpsilon;
        if (node.UseR && !isRotationZero)
        {
            let axis = node.Transform.rotation.slice(0, 3);
            let angleRad = math.degToRad(node.Transform.rotation[3]);
            transformedShape = this.#geometry.makeRotation(transformedShape, axis, angleRad);
        }

        const isTranslationZero = node.Transform.translation[0] === 0 && node.Transform.translation[1] === 0 && node.Transform.translation[2] === 0;
        if (node.UseT && !isTranslationZero)
        {
            transformedShape = this.#geometry.makeTranslate(transformedShape, node.Transform.translation);
        }
        
        transformedShape.userData = { ... shape.userData };

        this.#transformShapesUserData(transformedShape, node)
        //// console.log("transformShape() transformShape.userData: "+JSON.stringify(transformedShape.userData, null, 2));
        return transformedShape;
    }
    
    #transformShapesUserData(shape, node) {
        // For now only plane's point and normal are supported
        if (shape.userData.planePoint === undefined
        || shape.userData.planeNormal === undefined)
        {
            return;
        }
        
        let planePoint = shape.userData.planePoint;
        let planeNormal = shape.userData.planeNormal;

        if (node.Append === false)
        {
            planePoint = [0,0,0];
            planeNormal = [0,0,1];
        }
        
        const isScaleIdentity = node.Transform.scale[0] === 1 && node.Transform.scale[1] === 1 && node.Transform.scale[2] === 1;
        if (node.UseS && !isScaleIdentity)
        {
            planeNormal = this.#geometry.vectors.scale(planeNormal, node.Transform.scale)
        }

        const isRotationZero = node.Transform.rotation[3] <= math.kEpsilon;
        if (node.UseR && !isRotationZero)
        {
            let axis = node.Transform.rotation.slice(0, 3);
            let angleRad = math.degToRad(node.Transform.rotation[3]);
            // transformedShape = this.#geometry.makeRotation(transformedShape, axis, angleRad);
            planeNormal = this.#geometry.vectors.rotate(planeNormal, axis, angleRad);
        }

        const isTranslationZero = node.Transform.translation[0] === 0 && node.Transform.translation[1] === 0 && node.Transform.translation[2] === 0;
        if (node.UseT && !isTranslationZero)
        {
            // transformedShape = this.#geometry.makeTranslate(transformedShape, node.Transform.translation);
            planePoint = this.#geometry.vectors.translate(planePoint, node.Transform.translation);
        }
        
        shape.userData.planePoint = planePoint;
        shape.userData.planeNormal = planeNormal;
    }
}
