import _utils from './utils.js';
import shapes from '../occt/shapes'
import compounds from './compounds'
import { SVGEdgeType } from '../csg/csgTypes'

const wires = {

    setOpenCascade(openCascade) {
        this.openCascade = openCascade;
    },

    // export declare type GeomAbs_Shape = {
    //     GeomAbs_C0: {};
    //     GeomAbs_G1: {};
    //     GeomAbs_C1: {};
    //     GeomAbs_G2: {};
    //     GeomAbs_C2: {};
    //     GeomAbs_C3: {};
    //     GeomAbs_CN: {};
    // }
    /**
     * Converts from string into GeomAbs_Shape value.
     * @param continuity string like "C0" or "CN" or "G1"
     */
    parseContinuityAs_GeomAbs_Shape(continuity) {
        const oc = this.openCascade;
        let key = `GeomAbs_${continuity}`;
        return oc.GeomAbs_Shape[key];
    },

    /**
     * 
     * @param points An array of [x,y,z] points. [[x,y,z], [x,y,z], [x,y,z], ...]
     * @param closed should close the curve
     */
    makePolygonWireFromPoins(points, closed) {
        const oc = this.openCascade;

        points = [...points];
        if (closed) points.push(points[0]);
        
        // Create the wire
         let mkWire = new oc.BRepBuilderAPI_MakeWire_1();

        // let wire = new oc.BRepBuilderAPI_MakeWire_2(edge).Wire();
         
        for (let i = 0; i < points.length-1; i++) {
            let a = new oc.gp_Pnt_3(points[i][0], points[i][1], points[i][2]);
            let b = new oc.gp_Pnt_3(points[i+1][0], points[i+1][1], points[i+1][2]);
            
            let edge = new oc.BRepBuilderAPI_MakeEdge_3(a, b).Edge();
            mkWire.Add_1(edge);
        }

        // Create the polygon
        let polygon = mkWire.Wire();
        return polygon;
    },
    
    /**
     * Taken from OpenCascade
     * TODO: implement this
     * @param points An array of [x,y,z] points. [[x,y,z], [x,y,z], [x,y,z], ...]
     * @param closed should close the curve
     * @param curveContinuity for ex: oc.GeomAbs_Shape.GeomAbs_C0
     */
    fitBSplineWireFromPoints(points, closed, curveContinuity) {
        const oc = this.openCascade;
        
        let ptList = new oc.TColgp_Array1OfPnt_2(1, points.length + (closed ? 1 : 0));
        for (let pIndex = 1; pIndex <= points.length; pIndex++)
        {
            ptList.SetValue(pIndex, _utils.convertTo_gp_Pnt(points[pIndex - 1]));
        }
        
        if (closed) { ptList.SetValue(points.length + 1, ptList.Value(1)); }

        // let geomCurveHandle = new oc.GeomAPI_PointsToBSpline_2(ptList, 3, 8, oc.GeomAbs_Shape.GeomAbs_C0, 1.0e-3).Curve();
        let geomCurveHandle = new oc.GeomAPI_PointsToBSpline_2(ptList, 3, 8, curveContinuity, 1.0e-3).Curve();
        let geomCurveHandle2 = new oc.Handle_Geom_Curve_2(geomCurveHandle.get());
        let edge = new oc.BRepBuilderAPI_MakeEdge_24(geomCurveHandle2).Edge();
        let wire = new oc.BRepBuilderAPI_MakeWire_2(edge).Wire();
        // // console.log("wire: ",wire)
        
        return wire;
    },

    /**
     * TODO: implement closing the curve
     * @param points control points of the cubic bezier segment (multiples of 4)
     * @param closed
     * @return {TopoDS_Wire}
     */
    makeBezierWireFromControlPoints(points, closed) {
        const oc = this.openCascade;
        
        let controlPoints = new oc.TColgp_Array1OfPnt_2(1, points.length);
        for (let i = 1; i <= points.length; i++)
        {
            let pnt = _utils.convertTo_gp_Pnt(points[i-1]);
            controlPoints.SetValue(i, pnt);
        }
        
        let bezierCurve = new oc.Geom_BezierCurve_1(controlPoints);
        let geomCurveHandle2 = new oc.Handle_Geom_Curve_2(bezierCurve);
        let edge = new oc.BRepBuilderAPI_MakeEdge_24(geomCurveHandle2).Edge();
        let wire = new oc.BRepBuilderAPI_MakeWire_2(edge).Wire();
        
        return wire;
    },

    /**
     * 
     * @param svgEdges An array of SVGEdge
     * @param isClosed
     */
    makeBezierFromSVGPath(svgEdges, isClosed) {
        const oc = this.openCascade;

        let mkWire = new oc.BRepBuilderAPI_MakeWire_1();

        svgEdges.forEach(svgEdge => {
            switch(svgEdge.EdgeType)
            {
                case SVGEdgeType.Line: {
                    let a = new oc.gp_Pnt_3(svgEdge.Points[0][0], svgEdge.Points[0][1], 0);
                    let b = new oc.gp_Pnt_3(svgEdge.Points[1][0], svgEdge.Points[1][1], 0);

                    let edge = new oc.BRepBuilderAPI_MakeEdge_3(a, b).Edge();
                    mkWire.Add_1(edge);
                    break;
                }
                
                case SVGEdgeType.BezierQuadratic:
                case SVGEdgeType.BezierCubic: {
                    let controlPoints = _utils.convertTo_TColgp_Array1OfPnt(svgEdge.Points);
                    let bezierCurve = new oc.Geom_BezierCurve_1(controlPoints);
                    let geomCurveHandle2 = new oc.Handle_Geom_Curve_2(bezierCurve);
                    let edge = new oc.BRepBuilderAPI_MakeEdge_24(geomCurveHandle2).Edge();
                    mkWire.Add_1(edge);
                    break;
                }
            }
        });

        let svgPathWire = mkWire.Wire();
        return svgPathWire;
    },

    /**
     * 
     * @param svgPaths [ [svgPath], ...]
     */
    makeSVGPathsWire(svgPaths) {
        
        let wires = [];
        svgPaths.forEach(svgPath => {
            try {
                let wire = this.makeBezierFromSVGPath(svgPath.Edges, svgPath.IsClosed);
                wires.push(wire);    
            }
            catch(error)
            {
                console.error("error while making svg path wire: ", error);
            }
            
        });
        
        // console.log("wires: ",wires)
        
        let compound = compounds.makeCompound(wires);
        return compound;
    },

    /**
     * GeomAPI_Interpolate_2 DOES NOT WORK YET as occt.js is missing bindings to create the TColgp_HArray1OfPnt array of points, for details 
     * see this https://github.com/donalffons/opencascade.js/issues/87 
     * and this https://github.com/donalffons/opencascade.js/issues/33
     * @param points
     * @param closed
     * @return {TopoDS_Wire}
     */
    fitBezierWireFromPoints(points, closed) {
        const oc = this.openCascade;

        let controlPoints = new oc.TColgp_Array1OfPnt_2(1, points.length);
        for (let i = 1; i <= points.length; i++)
        {
            let pnt = _utils.convertTo_gp_Pnt(points[i-1]);
            controlPoints.SetValue(i, pnt);
        }
        
        let parameters = new oc.TColStd_Array1OfReal_2(1, points.length);
        let step = 1.0 / (points.length - 1);
        for (let i = 0; i < points.length; i++) {
            parameters.SetValue(i+1, i*step);
        }

        let controlPointsHandle = new oc.Handle_TColgp_HArray1OfPnt_2(controlPoints);
        
        let interp = new oc.GeomAPI_Interpolate_2(controlPointsHandle, parameters, false, 1.0e-6);

        let bezierCurve = interp.Curve();
        let geomCurveHandle2 = new oc.Handle_Geom_Curve_2(bezierCurve);
        let edge = new oc.BRepBuilderAPI_MakeEdge_24(geomCurveHandle2).Edge();
        let wire = new oc.BRepBuilderAPI_MakeWire_2(edge).Wire();
        
        return wire;
    },
    
    projectWireOntoFace(wire, target, d) {
        const oc = this.openCascade;

        let dir = new oc.gp_Dir_4(d[0], d[1], d[2]);
        
        let projection = new oc.BRepProj_Projection_1(wire, target, dir);
        // projection.Build();

        if (!projection.IsDone()) {
            throw new Error("Projecting a wire onto a shape failed.");
        }

        let projectedWire = projection.Shape();
        return projectedWire;
        
        // let projectedWire = new oc.TopoDS.Wire_1(projection.Shape());
        
        // TopoDS_Wire projectedWire = TopoDS::Wire();
        // use the projected wire
    },
    
    // offsetWire(wireShape, offset, joinType) {
    //     const oc = this.openCascade;
    //
    //     joinType = _utils.parseJoinType(joinType);
    //    
    //     wireShape = compounds.listWiresInsideCompound(wireShape)[0];
    //     let wire = new oc.TopoDS.Wire_1(wireShape);
    //
    //     let offseter = new oc.BRepOffsetAPI_MakeOffset_3(wire, joinType, false);
    //     offseter.Perform(offset, 0);
    //    
    //     if (!offseter.IsDone()) {
    //         throw new Error("Offsetting a wire failed.");
    //     }
    //
    //     let offsetedWire = offseter.Shape();
    //     return offsetedWire; 
    // },

    offsetWire(wireShape, offset, joinType) {
        const oc = this.openCascade;

        joinType = _utils.parseJoinType(joinType);
        
        let offsetedWires = [];
        compounds.listWiresInsideCompound(wireShape).forEach(wireShape => {
            let wire = new oc.TopoDS.Wire_1(wireShape);

            let offseter = new oc.BRepOffsetAPI_MakeOffset_3(wire, joinType, false);
            offseter.Perform(offset, 0);

            if (!offseter.IsDone()) {
                throw new Error("Offsetting a wire failed.");
            }

            let offsetedWire = offseter.Shape();
            offsetedWires.push(offsetedWire);
        });
        
        if (offsetedWires.length === 1)
        {
            return offsetedWires[0];
        }
        
        return compounds.makeCompound(offsetedWires);
    },

    /**
     * 
     * @param wiresShape A compound of wires.
     */
    reconstructWires(wireShapes) {
        wireShapes = wireShapes.map(wireShape => shapes.listWiresInsideCompound(wireShape)).flat(1);

        let edges = [];
        wireShapes.forEach(wireShape => {
            openCascadeHelper.ForEachEdge(wireShape, (index, edge) => {
                edges.push(edge);
                // console.log("### adding edge: ",edge);
                // console.log("### adding edge: of type: ", shapes.getShapeTypeAsString(edge));
            });
        })

        let edgesCompound = compounds.makeCompound(edges);
        let wiresCompound = new oc.TopoDS_Wire();
        let wireDone = oc.BOPAlgo_Tools.EdgesToWires(edgesCompound, wiresCompound, false, .001);

        // console.log("### wiresToFaces wireDone is: ",wireDone);
        // console.log("### wiresToFaces wiresCompound is: ",wiresCompound);
        
        return wiresCompound;
    }
    
};

export default wires;