import { CSGNodeType, CSGOperationType, CSGSolidType, CSGWireType } from './csgTypes'
import wires from "../common/wires";

export default class CSGSolidFactory
{
    #geometry;
    #nodeCalculator;
    
    constructor(geometry, nodeCalculator) {
        this.#geometry = geometry;
        this.#nodeCalculator = nodeCalculator;
    }

    /**
        return: a shape.
     */
    createNodeShape(node) {
        if (node.Type !== CSGNodeType.Solid) throw new Error(`Given node is not a ${CSGNodeType.Solid}`);

        let shape = undefined;

        switch(node.Solid.Type)
        {
            case CSGSolidType.Box:
                shape = this.#createBox(node.Solid);
                break;

            case CSGSolidType.Sphere:
                shape = this.#createSphere(node.Solid);
                break;

            case CSGSolidType.Cylinder:
                shape = this.#createCylinder(node.Solid);
                break;

            case CSGSolidType.Extrude:
                shape = this.#createExtrude(node.Solid);
                break;

            case CSGSolidType.ExtrudeWire:
                shape = this.#createExtrudeWire(node.Solid);
                break;

            case CSGSolidType.RevolveWire:
                shape = this.#createRevolveWire(node.Solid);
                break;

            case CSGSolidType.SweepWire:
                shape = this.#createSweepWire(node.Solid);
                break;
                
            default:
                throw new Error(`Solid type ${node.Solid.Type} is not supported.`)
        }

        return shape;
    }

    #createBox(solid) {
        let box = this.#geometry.makeBox(
            solid.Position[0],
            solid.Position[1],
            solid.Position[2],
            solid.Width,
            solid.Height,
            solid.Length,
            solid.PositionPivot[0],
            solid.PositionPivot[1],
            solid.PositionPivot[2]);
        
        return box;
    }

    #createSphere(solid) {
        let sphere = this.#geometry.makeSphere(
            solid.Position[0],
            solid.Position[1],
            solid.Position[2],
            solid.Radius);
        
        return sphere;
    }

    #createCylinder(solid) {
        let cylinder = this.#geometry.makeCylinder(
            [solid.Position[0], solid.Position[1], solid.Position[2]],
            [solid.Direction[0], solid.Direction[1], solid.Direction[2]],
            solid.Radius,
            solid.Height);

        return cylinder;
    }

    #createExtrude(solid) {
        
        const lines = solid.Sketch.Lines;
        const planePoint = solid.PlaneOrigin;
        const planeNormal = solid.PlaneNormal;
        const planeRotationZ = solid.PlaneRotationZ;
            
        const face = this.#geometry.makeFaceFromLinesAndPlane(lines, planePoint, planeNormal, planeRotationZ);
        // const dir = [0, 0, solid.LengthUp];
        let extrude = this.#geometry.sweeps.makeExtrude(face, planeNormal, solid.LengthUp, solid.LengthDown);
        return extrude;
    }

    #createExtrudeWire(solid) {
        let wireNode = solid.Wire;
        let direction = solid.Direction;
        let lengthUp = solid.LengthUp;
        let lengthDown = solid.LengthDown;

        let wireShape = this.#nodeCalculator.calculateNodeShapesWithCache(wireNode)[0];
        let face = this.#geometry.faces.wiresToFaces([wireShape]);
        let extrude = this.#geometry.sweeps.makeExtrude(face, direction, lengthUp, lengthDown);
        
        face.delete();
        
        return extrude;
    }

    #createRevolveWire(solid) {
        let wireNode = solid.Wire;
        let axisPoint = solid.AxisPoint;
        let axisDirection = solid.AxisDirection;
        let angleDeg = solid.Angle;

        let wireShape = this.#nodeCalculator.calculateNodeShapesWithCache(wireNode)[0];
        let revolve = this.#geometry.wireToSolid.revolveWireToMakeSolid(wireShape, axisPoint, axisDirection, angleDeg);

        return revolve;
    }
    
    #createSweepWire(solid) {
        let profileNode = solid.Profile;
        let pathNode = solid.Path;
        
        let profileShape = this.#nodeCalculator.calculateNodeShapesWithCache(profileNode)[0];
        let pathShape = this.#nodeCalculator.calculateNodeShapesWithCache(pathNode)[0];

        let sweep = this.#geometry.sweeps.pipeSolidFromWire(profileShape, pathShape);
        return sweep;
    }
}