    makeIntoSingleMesh_My(shape, addEdges, theLinDeflection, theAngDeflection) {
        const facelist = openCascadeHelper.tessellate(shape, theLinDeflection, theAngDeflection);
        
        let faces = facelist.map(face => {

            let vectors = [];

            for(let i = 0; i < face.vertex_coord.length; i += 3) {
                let vector = {
                    x: face.vertex_coord[i],
                    y: face.vertex_coord[i+1],
                    z: face.vertex_coord[i+2]
                };
                vectors.push(vector);
            }
            
            return {
                vertex_coord: vectors,
                vertexCount: vectors.length,
                
                // tri_indexes: triangles,
                // tri_indexes: [],
                tri_indexes: face.tri_indexes.map(index => index - 1),
                tri_indexesCount: face.tri_indexes.length,
                // number_of_triangles: tot_triangle_count,
            }
        });
        
        let edges = [];
        if (addEdges) {

            edges = facelist
                .map(face => face.edgeList).flat()
                .map((edge, index) => {
                    edge.original_edge_index = edge.edge_index;
                    edge.edge_index = index;

                    let vectors = [];

                    for (let i = 0; i < edge.vertex_coord.length; i += 3) {
                        let vector = {
                            x: edge.vertex_coord[i],
                            y: edge.vertex_coord[i + 1],
                            z: edge.vertex_coord[i + 2]
                        };
                        vectors.push(vector);
                    }

                    edge.vertex_coord = vectors;
                    return edge;
                });
        }
        
        let facesEdgesAlreadyAdded = edges.length > 0;
        if (addEdges && !facesEdgesAlreadyAdded)
        {
            const { edgeList } = visualize.ShapeToEdges(shape, theLinDeflection, theAngDeflection);
            edges = edges.concat(edgeList);

        }
        
        return { faces: faces, edges: edges };
        // return { faces: [face] }
    },

    getShapePosition_gp_XYZ(shape) {
        const oc = this.openCascade;
        
        if (shape.ShapeType() === oc.TopAbs_ShapeEnum.TopAbs_VERTEX)
        {
            let vertex = this.shapes.getActualTypeOfShape(shape);
            let pnt = oc.BRep_Tool.Pnt(vertex);
            return pnt.XYZ();
        }
        
        let shapeTranslationPart_gp_XYZ = shape.Location_1().Transformation().TranslationPart();
        return shapeTranslationPart_gp_XYZ;
    },

    getShapeRotation_gp_Quaternion(shape) {
        let rotation_gp_Quaternion = shape.Location_1().Transformation().GetRotation_2();
        return rotation_gp_Quaternion;
    },

    /**
     * Taken from: https://www.euclideanspace.com/maths/geometry/rotations/conversions/quaternionToAngle/index.htm
     * @param q
     * @return { angle: Number, axis:[x, y, z] }
     */
    getQuaternionAngleAxis_Custom(q) {
        // //// console.log(`quaternion is q.X: ${q.X()} q.Y: ${q.Y()} q.Z: ${q.Z()} q.W: ${q.W()}`)

        const oc = this.openCascade;

        let angleRad = 2 * Math.acos(q.W())
        let s = Math.sqrt(1-q.W()*q.W());

        // case where angle is 0 (qw == 0)
        if (s === 0) s = 1;

        let axisX = q.X() / s;
        let axisY = q.Y() / s;
        let axisZ = q.Z() / s;

        if (s === 0) axisX = 1;

        return { angle: angleRad, axis: [axisX, axisY, axisZ]} 
    },
    
    getQuaternionAngleAxis(q) {
        // //// console.log(`quaternion is q.X: ${q.X()} q.Y: ${q.Y()} q.Z: ${q.Z()} q.W: ${q.W()}`)

        const oc = this.openCascade;
        
        let ref_vec = new oc.gp_Vec_1();
        let ref_angle = { current: 0 };
        q.GetVectorAndAngle(ref_vec, ref_angle);
        
        return { angle: ref_angle.current, axis: [ref_vec.X(), ref_vec.Y(), ref_vec.Z()]}
    },
    
    /**
     * 
     * @param points An array of arrays. Each sub array is a 3d point [[x,y,z], [x,y,z], (...)]
     * @returns {TopoDS_Compound}
     */
    makePolygon(points) {
        
        if (points.length < 3) throw new Error('Cannot make polygon out of less than 3 points.');
        if (points.length < 4) throw new Error('Cannot make polygon out of more than 4 points.');
        
        let openCascade = this.openCascade;

        const builder = new openCascade.BRep_Builder();
        const   aComp = new openCascade.TopoDS_Compound();
        builder.MakeCompound(aComp);
        
        // const path = [[-50, 0, 0], [50, 0, 0], [50, 100, 0]].map(([x, y, z]) => new openCascade.gp_Pnt_3(x, y, z));
        const path = points.map(([x, y, z]) => new openCascade.gp_Pnt_3(x, y, z));
        
        const makePolygon = points.length === 3 
            ? new openCascade.BRepBuilderAPI_MakePolygon_3(path[0], path[1], path[2], true)
            : new openCascade.BRepBuilderAPI_MakePolygon_4(path[0], path[1], path[2], path[3], true);
            
        const wire = makePolygon.Wire();
        const f = new openCascade.BRepBuilderAPI_MakeFace_15(wire, false);
        
        builder.Add(aComp, f.Shape());
        return aComp;
    },
    
    makeSphere(x, y, z, radius) {
        const oc = this.openCascade;

        let p = new oc.gp_Pnt_3(0,0,0);
        let sphereShape = new oc.BRepPrimAPI_MakeSphere_5(p, radius).Shape();
        sphereShape = this.doTranslate(sphereShape, [x, y, z]);
        
        return sphereShape;
    },

