        // let sewingBuilder = new oc.BRepBuilderAPI_FastSewing(0.01);
        // sewingBuilder.Add_1(shell);
        // sewingBuilder.Add_1(startFace);
        // sewingBuilder.Add_1(endFace);
        // sewingBuilder.Perform();
        //
        // let closedShell = sewingBuilder.GetResult();
