export default class Performer
{
    #lastMeasureMs;
    
    constructor() {
        this.timeStart = 0;
    }

    static create() {
        return new Performer();
    }
    
    start() {
        this.timeStart = new Date().getTime();
        return this;
    }

    measure(label) {
        let timeEndLayout = new Date().getTime();
        let durationInMs = (timeEndLayout - this.timeStart);
        console.log("#perf# "+label +" "+ durationInMs +" ms.")
        
        this.#lastMeasureMs = durationInMs;
        return durationInMs;
    }
    
    lastMeasure() {
        return this.#lastMeasureMs;
    }
}