

import {
    BRep_Builder,
    OpenCascadeInstance,
    TopoDS_Shape,
} from "opencascade.js/dist/opencascade.full";

const occtio = {
    oc: null as OpenCascadeInstance | null,

    setOpenCascade(openCascade: OpenCascadeInstance) {
        this.oc = openCascade;
    },

    /**
     * https://github.com/donalffons/opencascade.js/issues/222
     * 
     * @param shape
     * @param points
     */
    shapeToBRep(shape : TopoDS_Shape) : string {
        const oc = this.oc!;
        
        var theProgress = new oc.Message_ProgressRange_1();
        var result = oc.BRepTools.Write_3(shape,"test",theProgress)
        let brep = oc.FS.readFile("/" + "test", { encoding:"utf8" } as any);
        oc.FS.unlink("/" + "test");
        console.log(result)
        console.log(brep)
        
        return brep as any;
    },

    bRepToShape(shapeBRepText : string) : TopoDS_Shape{
        const oc = this.oc!;
        
        var theProgress = new oc.Message_ProgressRange_1();
        
        oc.FS.writeFile("/" + "test", shapeBRepText, { encoding:"utf8" } as any);

        let shape : TopoDS_Shape = new oc.TopoDS_Shape();
        let bRepBuilder = new oc.BRep_Builder();
        var ok = oc.BRepTools.Read_2(shape,"test", bRepBuilder, theProgress);
        
        oc.FS.unlink("/" + "test");
        
        if (!ok)
        {
            throw new Error("Could not read a shape from string:\n"+shapeBRepText);
        }
        
        return shape;
    }
};

export default occtio;