import math from "../common/math";
import compounds from "../common/compounds";
import { OpenCascadeInstance, TopoDS_Shape, Message_ProgressRange_1, BRepAlgoAPI_Fuse_3, ShapeUpgrade_UnifySameDomain_2 } from "opencascade.js/dist/opencascade.full";

const booleans = {
    oc: null as OpenCascadeInstance | null,

    setOpenCascade(openCascade: OpenCascadeInstance) {
        this.oc = openCascade;
    },

    makeUnion(shapesToJoin: TopoDS_Shape[], keepEdges: boolean) {
        const oc = this.oc!;

        let explodedShapes = shapesToJoin
            .map(shape => compounds.deCompoundShape(shape))
            .flat(1);

        let combined = explodedShapes[0];
        
        for (let i = 1; i < explodedShapes.length; i++) {
            let shape1 = combined;
            let shape2 = explodedShapes[i];

            let progressRange = new oc.Message_ProgressRange_1();
            let combinedFuse = new oc.BRepAlgoAPI_Fuse_3(shape1, shape2, progressRange);

            combined = combinedFuse.Shape();
            
            progressRange.delete();
            combinedFuse.delete();
        }

        if (!keepEdges) {
            let fusor = new oc.ShapeUpgrade_UnifySameDomain_2(combined, true, false, false);
            
            fusor.Build();
            combined = fusor.Shape();
            
            fusor.delete();
        }
        
        return combined;
    },
};

export default booleans;
