import {CSGNodeType, CSGOperationType, CSGNodeMetadataKind} from "./csgTypes";
import CSGSolidFactory from './csgSolidFactory.js'
import CSGPlaneFactory from './csgPlaneFactory.js'
import CSGWireFactory from './csgWireFactory.js'
import CSGWireOperationCalculator from './csgWireOperationCalculator.js'
import CSGFaceFactory from './csgFaceFactory.js'
import CSGOperationCalculator from './csgOperationCalculator.js'
import CSGTransformCalculator from './csgTransformCalculator.js'
import Performer from './../common/performer'
import ff from './../common/featureFlags'
import CSGVertexFactory from "./csgVertexFactory.js";

export default class CSGNodeCalculator
{
    #geometry;
    #solidFactory;
    #planeFactory;
    #wireFactory;
    #faceFactory;
    #vertexFactory;
    #wireOperationCalculator;
    #operationCalculator;
    #transformCalculator;
    #nodeCacheMap;
    
    constructor(geometry) {
        this.#geometry = geometry;
        this.#solidFactory = new CSGSolidFactory(geometry, this);
        this.#planeFactory = new CSGPlaneFactory(geometry);
        this.#wireFactory = new CSGWireFactory(geometry);
        this.#faceFactory = new CSGFaceFactory(geometry, this);
        this.#vertexFactory = new CSGVertexFactory(geometry, this);
        this.#wireOperationCalculator = new CSGWireOperationCalculator(geometry, this);
        this.#operationCalculator = new CSGOperationCalculator(geometry, this);
        this.#transformCalculator = new CSGTransformCalculator(geometry, this);
        this.#nodeCacheMap = new Map();
    }

    /**
     * Unity's API entry point to calculating CSGNode result.
     * 
     * @param node CSGNode input to calculate.
     * @returns {string} A text representation list of topo shapes.
     */
    calculateNodeResult(node)
    {
        // // console.log("######### calculateNodeResult abcdefgh #########")
        
        try {
            let perf = Performer.create().start();
            
            if (typeof node === 'string')
            {
                let perfParseJson = Performer.create().start();
                node = JSON.parse(node);
                perfParseJson.measure("calculateNodeResult() parse input node json node Id: "+node.PartName)
            }

            let perfCalculateNodeShape = Performer.create().start();
            let shapes = this.calculateNodeShapesWithCache(node);
            perfCalculateNodeShape.measure("calculateNodeResult() calculate node shapes for node Id: "+node.PartName)
            
            let topoShapesData = {
                topoShapes: []
            };
            
            shapes.forEach((shape, shapeIndex) => {
                this.appendTopoShapeData(node, shape, topoShapesData, shapeIndex);
            });

            perf.measure("calculateNodeResult() node Id: "+node.PartName)
            node._duration = perf.lastMeasure();

            let perfJson = Performer.create().start();
            let topoShapesDataJson = JSON.stringify(topoShapesData, null, 2);
            // console.log("C# topoShapesDataJson length: "+topoShapesDataJson.length)
            // console.log("C# typeof topoShapesDataJson: "+ (typeof topoShapesDataJson))
            perfJson.measure("calculateNodeResult() stringify topoShapeDataJson node Id: \"+node.PartName+\"");
            
            return topoShapesDataJson;
        }
        catch (e)
        {
            const oc = this.#geometry.openCascade;
            
            let errorMessage = "";
            
            if(typeof e === "number") {
                const exceptionData = oc.OCJS.getStandard_FailureData(e);
                // console.log(`That didn't work because: ${exceptionData.GetMessageString()}`);
                errorMessage = `That didn't work because: ${exceptionData.GetMessageString()}`;
            } else if (typeof e === "string") {
                errorMessage = e;
                // console.log();
            } else {
                errorMessage = "Unkown error: "+e; 
                // console.log();
            }
            
            console.error(`${this.constructor.name}.calculateNodeResult(${node.PartName} error: ${errorMessage}`);
            
            return JSON.stringify({
                error: `${errorMessage}`
            });
            
            // throw e;
        }
    }

    appendTopoShapeData(node, shape, topoShapesData, shapeIndex) {
        let perfMeshing = Performer.create().start();

        let meshDto = node.MetadataRequests.find(dto => dto.Kind === CSGNodeMetadataKind.Mesh);
        const {faceListData, edgeListData} = meshDto !== undefined
            ? this.#prepareResultMeshForShape(shape, meshDto.TheLinDeflection, meshDto.TheAngDeflection)
            : { faceListData: {faces: []}, edgeListData: {edges: []} };
        
        perfMeshing.measure("calculateNodeResult() mesh shape nr "+(shapeIndex++)+" node: "+node.PartName);

        let p = this.#geometry.getShapePosition_gp_XYZ(shape);
        let q = this.#geometry.getShapeRotation_gp_Quaternion(shape);
        const {angle, axis} = this.#geometry.getQuaternionAngleAxis(q);

        let topoShapeData = {
            mesh: {
                faceListData: faceListData,
                edgeListData: edgeListData,    
            },
           
            translation: {x: p.X(), y: p.Y(), z: p.Z(),},
            scale: {x: 1, y: 1, z: 1},
            rotation: {x: axis[0], y: axis[1], z: axis[2], angleRad: angle}
        }

        if (ff.sendBackNodeDebugData)
        {
            topoShapeData._node = node;
        }

        if (ff.sendBackShapeVerticesData)
        {
            topoShapeData.verticesData = {
                vertices: this.#geometry.shapes.listShapeVerticesAsPoints(shape)
                    .map(gp_pnt => [gp_pnt.X(), gp_pnt.Y(), gp_pnt.Z()])
            };
        }

        if (ff.sendBackShapeUserData)
        {
            let aabb = node.MetadataRequests.find(dto => dto.Kind === CSGNodeMetadataKind.AABB) !== undefined
                ? this.#geometry.boundingBoxes.getAABB(shape, false)
                : {};

            let obb = node.MetadataRequests.find(dto => dto.Kind === CSGNodeMetadataKind.OBB) !== undefined
                ? this.#geometry.boundingBoxes.getOBB(shape, false)
                : {};

            topoShapeData.obb = obb;
            topoShapeData.aabb = aabb;
            
            topoShapeData._shapeUserData = shape.userData ?? { isEmpty: true };
        }

        topoShapeData.ShapeType = this.#geometry.shapes.getShapeTypeAsString(shape);
        
        topoShapesData.topoShapes.push(topoShapeData);
        shape.topoShapeData = topoShapeData;
        
        console.log("### topoShapeData: "+topoShapeData);
    }
    
    /**
     * 
     * @param node
     * @param explodeShapes If true all the shapes will be put into cache individually, thats to support 'disjoin' operation which produces multiple shapes out of single compound shape.
     * 
     * @returns calculated shapes. 
     */
    calculateNodeShapesWithCache(node) {
        
        let result = this.#nodeCacheMap.get(node.PartName);
        if (result !== undefined)
        {
            return result.shapes;
        }
        
        let shapes = this.#calculateNodeShapes(node);

        let explodeShapes = node.Type === CSGNodeType.Operation && node.Operation === CSGOperationType.Disjoin;
        if (!explodeShapes)
        {
            this.#nodeCacheMap.set(node.PartName, {
                shapes: shapes
            });    
        }
        else
        {
            shapes.forEach((shape, index) => {
                let id = index < 1 ? `${node.PartName}` : `${node.PartName}_${index}`;
                this.#nodeCacheMap.set(id, {
                    shapes: [ shape ]
                });
            })
        }
        
        return shapes;
    }

    /**
     * 
     * @param node
     * @returns {*} [ shape, ... ] array of result shapes (a single shape for most of the CSGNodes) 
     */
    #calculateNodeShapes(node) {
        //// console.log(`${this.constructor.name}.calculateNodeShape(${node.PartName} called...`);
        
        let shapes = undefined;
        
        let perf = Performer.create().start();
        
        switch(node.Type)
        {
            case CSGNodeType.Reference:
                shapes = this.#calculateReferenceNodeShapes(node);
                break;
            
            case CSGNodeType.Solid:
                shapes = [ this.#solidFactory.createNodeShape(node) ];
                break;

            case CSGNodeType.Plane:
                shapes = [ this.#planeFactory.createNodeShape(node) ];
                break;

            case CSGNodeType.Operation:
                shapes = this.#operationCalculator.calculateOperation(node);
                break;

            case CSGNodeType.Wire:
                shapes = [ this.#wireFactory.createNodeShape(node) ];
                break;

            case CSGNodeType.WireOperation:
                shapes = this.#wireOperationCalculator.calculateOperation(node);
                break;

            case CSGNodeType.Face:
                shapes = [ this.#faceFactory.createNodeShape(node) ];
                break;
            
            case CSGNodeType.Vertex:
                shapes = [ this.#vertexFactory.createNodeShape(node) ];
                break;
                
            case CSGNodeType.Transform:
                shapes = this.#transformCalculator.calculateTransforms(node);
                break;
                
            default:
                throw new Error(`Node type ${node.Type} is unsupported yet.`);
        }
        
        perf.measure("calculateNodeShape() node: "+node.PartName)
        node._calculateNodeShapeDuration = perf.lastMeasure();
        
        return shapes;
    }

    #calculateReferenceNodeShapes(node) {
        if (node.Type !== CSGNodeType.Reference) throw new Error(`Given node is not a ${CSGNodeType.Reference}`);
        
        let cachedValue = this.#nodeCacheMap.get(node.ReferencePartName);
        if (cachedValue === undefined)
        {
            throw new Error(`${this.constructor.name}.calculateReferenceNode(${node.PartName}) cache missed looking for reference node: ${node.ReferencePartName}.`);
        }
        
        return cachedValue.shapes;
    }
    
    #prepareResultMeshForShape(shape, theLinDeflection, theAngDeflection)
    {
        // const oc = this.#geometry.openCascade;

        // if (shape.ShapeType() === oc.TopAbs_ShapeEnum.TopAbs_WIRE)
        // {
        //     // let facesAndEdges = this.#prepareResultMesh2(shape);
        //     let facesAndEdges = this.#geometry.visualize.ShapeToMesh(shape, 0.01, 0.05);
        //
        //     let faceListData = {
        //         faces: facesAndEdges[0]
        //     };
        //     let edgeListData = {
        //         // edges: []
        //         edges: facesAndEdges[1]
        //     };
        //
        //     return {faceListData, edgeListData};
        // }

        {
            // let faceListData = this.#prepareResultMesh(shape);
            // let edgeListData = { edges: []};
            
            let facesAndEdges = this.#geometry.makeIntoSingleMesh_My(shape, true, theLinDeflection, theAngDeflection);
            
            let faceListData = {faces: facesAndEdges.faces};
            let edgeListData = {edges: facesAndEdges.edges};
            // let edgeListData = {edges: []};
            
            return {faceListData, edgeListData};
        }
    }
    
    // #prepareResultMesh(shape)
    // {
    //     // let result = this.#geometry.makeIntoSingleMesh(shape);
    //     let result = this.#geometry.makeIntoSingleMesh_My(shape, false);
    //     return result;
    // }

    // #prepareResultMesh2(shape)
    // {
    //     return this.#geometry.visualize.ShapeToMesh(shape, 0.1);
    // }
}
