import {CSGNodeType, CSGOperationType} from "./csgTypes";
import ff from './../common/featureFlags'

export default class CSGOperationCalculator
{
    #geometry;
    #nodeCalculator;
    
    constructor(geometry, nodeCalculator) {
        this.#geometry = geometry;
        this.#nodeCalculator = nodeCalculator;
    }

    calculateOperation(node) {
        if (node.Type !== CSGNodeType.Operation) throw new Error(`Given node is not an ${CSGNodeType.Operation}`);
        
        let shapes = undefined;

        switch(node.Operation)
        {
            case CSGOperationType.Union:
                shapes = this.#union(node.Nodes);
                break;

            case CSGOperationType.Difference:
                shapes = this.#difference(node.Nodes);
                break;

            case CSGOperationType.Intersection:
                shapes = this.#intersection(node.Nodes);
                break;

            case CSGOperationType.Clone:
                shapes = this.#clone(node.Nodes);
                break;

            case CSGOperationType.Join:
                shapes = this.#join(node.Nodes);
                break;

            case CSGOperationType.Disjoin:
                shapes = this.#disjoin(node.Nodes);
                break;
                
            case CSGOperationType.SliceOff:
                shapes = this.#sliceOff(node.Nodes);
                break;

            case CSGOperationType.Offset:
                shapes = this.#offset(node.Nodes, node.OffsetValue, node.OffsetMode);
                break;

            case CSGOperationType.Hollow:
                shapes = this.#hollow(node.Nodes, node.FaceIndexes, node.WallThickness, node.JoinType);
                break;

            default:
                throw new Error(`Node operation ${node.Operation} is unsupported yet.`);
        }

        if (ff.attachCSGNodeToShapeResult)
        {
            shapes.forEach(shape => {
                shape._node = node;
            });
        }

        return shapes;
    }

    #union(nodes) {
        let shapes = nodes.map( node => this.#nodeCalculator.calculateNodeShapesWithCache(node) ).flat(1);
        let union = this.#geometry.booleans.makeUnion(shapes);
        return [ union ];
    }

    #difference(nodes) {
        let shapes = nodes.map( node => this.#nodeCalculator.calculateNodeShapesWithCache(node) ).flat(1);
        
        let targetShape = shapes[0];
        let toolShapes = shapes.slice(1)
        
        // let difference = this.#geometry.makeDifference(targetShape, toolShapes);
        let difference = this.#geometry.makeDifferenceMulti([targetShape], toolShapes);
        
        return [ difference ];
    }

    #intersection(nodes) {
        let shapes = nodes.map( node => this.#nodeCalculator.calculateNodeShapesWithCache(node) ).flat(1);
        let intersection = this.#geometry.makeIntersection(shapes);
        return [ intersection ];
    }

    #clone(nodes) {
        let shapes = nodes.map( node => this.#nodeCalculator.calculateNodeShapesWithCache(node) ).flat(1);
        let cloned = this.#geometry.makeClone(shapes[0]);
        
        cloned.userData = { ... shapes[0].userData };
        return [ cloned ];
    }

    #join(nodes) {
        let shapes = nodes.map( node => this.#nodeCalculator.calculateNodeShapesWithCache(node) ).flat(1);
        let joined = this.#geometry.compounds.makeCompound(shapes);
        return [ joined ];
    }

    #disjoin(nodes) {
        let shapes = nodes.map( node => this.#nodeCalculator.calculateNodeShapesWithCache(node) ).flat(1);
        
        let explodedShapes = shapes.map(shape => {
            let disjoined = this.#geometry.explodeCompound(shape);
            return disjoined;
        }).flat(1);
        
        return explodedShapes;
    }

    #sliceOff(nodes) {
        let shapes = nodes.map( node => this.#nodeCalculator.calculateNodeShapesWithCache(node) ).flat(1);

        let sliced = shapes[0];
        for(let i=1; i<shapes.length; i++)
        {
            const planeShape = shapes[i];
            sliced = this.#geometry.makeSplitWithPlane(
                sliced,
                planeShape,
                planeShape.userData.planePoint,
                planeShape.userData.planeNormal);
        }

        return [ sliced ];
    }
    
    #offset(nodes, offset, mode) {
        let shapes = nodes.map( node => this.#nodeCalculator.calculateNodeShapesWithCache(node) ).flat(1);
        let thickened = this.#geometry.makeOffset(shapes[0], offset, mode);
        return [ thickened ];
    }

    /**
     * 
     * @param nodes [node, ...]
     * @param faceIndexes [1, 2, 3, ...]
     * @return {*[]}
     */
    #hollow(nodes, faceIndexes, wallThickness, joinType) {
        let shapes = nodes.map( node => this.#nodeCalculator.calculateNodeShapesWithCache(node) ).flat(1);
        let hollowed = this.#geometry.solidOperations.makeHollow(shapes[0], faceIndexes, wallThickness, joinType);

        return [ hollowed ];
    }
}
